<template>
  <div class="seller" ref="seller">
    <div class="seller-content">
      <div class="overview">
        <div class="title">{{seller.name}}</div>
        <div class="desc">
          <star :size="36" :score="seller.score"></star>
          <span class="text">({{seller.ratingCount}})</span>
          <span class="text">月售{{seller.sellCount}}单</span>
        </div>
        <ul class="remark">
          <li class="block">
            <h2>起送价</h2>
            <div class="content">
              <span class="stress">{{seller.minPrice}}</span>元
            </div>
          </li>
          <li class="block">
            <h2>商家配送</h2>
            <div class="content">
              <span class="stress">{{seller.deliveryPrice}}</span>元
            </div>
          </li>
          <li class="block">
            <h2>平均配送时间</h2>
            <div class="content">
              <span class="stress">{{seller.deliveryTime}}</span>分钟
            </div>
          </li>
        </ul>
        <div class="favorite" @click="toggleFavorite">
          <span class="icon-favorite" :class="{'active':favorite}"></span>
          <span class="text">{{favoriteText}}</span>
        </div>
      </div>
      <split></split>
      <div class="bulletin">
        <h1 class="title">公告与活动</h1>
        <div class="content-wrapper border-1px">
          <p class="content">{{seller.bulletin}}</p>
        </div>
        <ul v-if="seller.supports" class="supports">
          <li class="support-item border-1px" v-for="(item,index) in seller.supports" :key="index">
            <span class="icon" :class="classMap[seller.supports[index].type]"></span>
            <span class="text">{{seller.supports[index].description}}</span>
          </li>
        </ul>
      </div>
      <split></split>
      <div class="pics">
        <h1 class="title">商家实景</h1>
        <div class="pic-wrapper" ref="picWrapper">
          <ul class="pic-list" ref="picList">
            <li class="pic-item" v-for="pic in seller.pics" :key="pic.index">
              <img :src="pic" width="120" height="90">
            </li>
          </ul>
        </div>
      </div>
      <split></split>
      <div class="info">
        <h1 class="title border-1px">商家信息</h1>
        <ul>
          <li class="info-item" v-for="info in seller.infos" :key="info.index">{{info}}</li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import BScroll from 'better-scroll'
import star from 'components/star/star'
import split from 'components/split/split'
import { saveToLocal, loadFromLocal } from 'common/js/store'
export default {
  props: {
    seller: {
      type: Object,
      default () {
        return []
      }
    }
  },
  data () {
    return {
      classMap: [],
      favorite: (() => {
        return loadFromLocal(this.seller.id, 'favorite', false)
      })()
    }
  },
  computed: {
    favoriteText () {
      return this.favorite ? '已收藏' : '收藏'
    }
  },
  methods: {
    toggleFavorite (event) {
      if (!event._constructed) {
        return
      }
      this.favorite = !this.favorite
      saveToLocal(this.seller.id, 'favorite', this.favorite)
    },
    _initScroll () {
      if (!this.scroll) {
        this.scroll = new BScroll(this.$refs.seller, {
          click: true
        })
      } else {
        this.scroll.refresh()
      }
    },
    _initPics () {
      if (this.seller.pics) {
        let picWidth = 120
        let picMargin = 6
        let width = (picWidth + picMargin) * this.seller.pics.length - picMargin
        this.$refs.picList.style.width = width + 'px'
        this.$nextTick(() => {
          if (!this.picScroll) {
            this.picScroll = new BScroll(this.$refs.picWrapper, {
              scrollX: true,
              eventPassthrough: 'vertical'
            })
          } else {
            this.picScroll.refresh()
          }
        })
      }
    }
  },
  watch: {
    'seller' () {
      this._initScroll()
      this._initPics()
    }
  },
  components: {
    star, split
  },
  created () {
    this.classMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee']
  },
  mounted () {
    // 此处的函数 都是在DOM已经加载完成之后才执行的
    this._initScroll()
    this._initPics()
  }
}
</script>

<style scoped lang="stylus">
@import '../../common/stylus/mixin'
@import '../../common/stylus/icon'

.seller
  position absolute
  top 174px
  left 0
  bottom 0
  width 100%
  overflow hidden

  .overview
    padding 18px

    .title
      margin-bottom 8px
      line-height 14px
      font-size 14px
      color rgb(7, 17, 27)
      font-weight 400

    .desc
      padding-bottom 18px
      line-height 18px
      font-size 0
      border-1px(rgba(7, 17, 27, 0.1), 'bottom')

      .star
        display inline-block
        margin-right 8px
        vertical-align top

      .text
        display inline-block
        margin-right 12px
        line-height 18px
        font-size 10px
        font-weight 400
        vertical-align top
        color rgb(77, 85, 93)

    .remark
      display flex
      padding-top 18px

      .block
        flex 1
        text-align center
        border-right 1px solid rgba(7, 17, 27, 0.1)

        &:last-child
          border none

        h2
          margin-bottom 8px
          line-height 10px
          font-size 10px
          color rgb(147, 153, 159)

        .content
          line-height 24px
          font-size 10px
          color rgb(7, 17, 27)

          .stress
            font-size 24px

    .favorite
      position absolute
      width 50px
      right 11px
      top 18px
      text-align center

      .icon-favorite
        display block
        margin-bottom 4px
        line-height 24px
        font-size 24px
        color #d4d6d9

        &.active
          color rgb(240, 20, 20)

      .text
        line-height 10px
        font-size 10px
        color rgb(77, 85, 93)

  .bulletin
    padding 18px 18px 0 18px

    .title
      margin-bottom 8px
      line-height 14px
      color rgb(7, 17, 27)
      font-size 14px
      font-weight 400

    .content-wrapper
      padding 0 12px 16px 12px
      border-1px(rgba(7, 17, 27, 0.1), 'bottom')

      .content
        line-height 24px
        font-size 12px
        font-weight 400
        color #ea6464

    .supports
      .support-item
        padding 16px 12px
        border-1px(rgba(7, 17, 27, 0.1), 'bottom')
        font-size 0

        &:last-child
          border-none()

      .icon
        display inline-block
        width 16px
        height 16px
        vertical-align top
        margin-right 6px
        background-size 16px 16px
        background-repeat no-repeat

        &.decrease
          bg-image('decrease_4')

        &.discount
          bg-image('discount_4')

        &.guarantee
          bg-image('guarantee_4')

        &.invoice
          bg-image('invoice_4')

        &.special
          bg-image('special_4')

      .text
        line-height 16px
        font-size 12px
        color rgb(7, 17, 27)

  .pics
    padding 18px

    .title
      margin-bottom 12px
      line-height 14px
      color rgb(7, 17, 27)
      font-size 14px
      font-weight 400

    .pic-wrapper
      width 100%
      overflow hidden
      white-space nowrap

      .pic-list
        font-size 0

        .pic-item
          display inline-block
          margin-right 6px
          width 120px
          height 90px

          &:last-child
            margin 0

  .info
    padding 18px 18px 0 18px
    color rgb(7, 17, 27)

    .title
      padding-bottom 12px
      line-height 14px
      font-size 14px
      font-weight 400

    .info-item
      padding 16px 12px
      line-height 16px
      border-1px(rgba(7, 17, 27, 0.1), 'top')
      font-size 12px
</style>



