<template>
  <transition name="slide">
    <div class="food" v-show="showFlag" ref="food">
      <div class="food-content">
        <div class="image-header">
          <img :src="food.image">
          <div class="back" @click="back">
            <i class="icon icon-arrow_lift"></i>
          </div>
        </div>
        <div class="content">
          <h1 class="title">{{food.name}}</h1>
          <div class="detail">
            <span class="sell-count">月售{{food.sellCount}}份</span>
            <span class="rating">好评率{{food.rating}}%</span>
          </div>
          <div class="price">
            <span class="new">¥{{food.price}}</span>
            <span class="old" v-show="food.oldPrice">原价：{{food.oldPrice}}元</span>
          </div>
          <div class="cartcontrol-wrapper">
            <cartcontrol :food="food" ref="cart" @cart-add="cartAdd"></cartcontrol>
          </div>
          <transition name="scale">
            <div class="buy" @click="addFirst($event)" v-show="!food.count||food.count===0">加入购物车</div>
          </transition>
        </div>
        <split v-show="food.info"></split>
        <div class="info" v-show="food.info">
          <h1 class="title">商品信息</h1>
          <div class="text">{{food.info}}</div>
        </div>
        <split v-show="food.rating"></split>
        <div class="rating">
          <h1 class="title">商品评价</h1>
          <ratingselect :select-type="selectType" :only-content="onlyContent" :desc="desc" :ratings="food.ratings" @on-onlyContent="_onlyContent" @on-selectType="_selectType"></ratingselect>
          <div class="rating-wrapper">
            <ul v-show="food.ratings&&food.ratings.length">
              <li v-for="rating in food.ratings" :key="rating.index" v-show="isItemShow(rating.rateType,rating.text)" class="rating-item">
                <div class="user">
                  <span class="name">{{rating.name}}</span>
                  <img :src="rating.avatar" width="12" height="12" class="avatar">
                </div>
                <div class="time">{{rating.rateTime|formatDate}}</div>
                <p class="text">
                  <span :class="{'icon-thumb_up':rating.rateType===0,'icon-thumb_down':rating.rateType===1}"></span>
                  {{rating.text}}
                </p>
              </li>
            </ul>
            <div class="no-rating" v-show="!food.ratings||!food.ratings.length">
              暂无评价
            </div>
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>

<script type="text/ecmascript-6">
import BScroll from 'better-scroll'
import cartcontrol from 'components/cartcontrol/cartcontrol'
import ratingselect from 'components/ratingselect/ratingselect'
import split from 'components/split/split'
import Vue from 'vue'
import moment from 'moment'
Vue.filter('formatDate', value => {
  return moment(value).format('YYYY-MM-DD h:mm')
})
/* eslint-disable no-unused-vars */
const POSITIVE = 0
const NEGATIVE = 1
const ALL = 2
export default {
  props: {
    food: {
      type: Object,
      default () {
        return {}
      }
    }
  },
  data () {
    return {
      showFlag: false,
      selectType: ALL,
      onlyContent: true,
      desc: {
        all: '全部',
        positive: '满意',
        negative: '吐槽'
      }
    }
  },
  methods: {
    show () {
      this.showFlag = true
      this.selectType = 2
      this.onlyContent = true
      this.$nextTick(() => {
        if (!this.scroll) {
          this.scroll = new BScroll(this.$refs.food, {
            click: true
          })
        } else {
          this.scroll.refresh()
        }
      })
    },
    back () {
      this.showFlag = false
    },
    addFirst (event) {
      if (!event._constructed) {
        return
      }
      console.log('click')
      Vue.set(this.food, 'count', 1)
      this.$emit('cart-add', event.target)
    },
    cartAdd (target) {
      this.$emit('cart-add', event.target)
    },
    isItemShow (type, text) {
      if (this.onlyContent && !text) {
        return false
      }
      if (this.selectType === ALL || this.selectType === type) {
        return true
      }
    },
    _selectType (type) {
      this.selectType = type
      this.$nextTick(() => {
        this.scroll.refresh()
      })
    },
    _onlyContent (boolean) {
      this.onlyContent = boolean
      this.$nextTick(() => {
        this.scroll.refresh()
      })
    }
  },
  components: {
    cartcontrol,
    split,
    ratingselect
  }
}
</script>

<style scoped lang="stylus">
@import '../../common/stylus/mixin'

.food
  position fixed
  top 0
  left 0
  bottom 48px
  width 100%
  z-index 30
  background #fff
  transform translate3d(0, 0, 0)

  &.slide-enter-active, &.slide-leave-active
    transition all 0.3s cubic-bezier(0.5, 0.01, 0.18, 1)

  &.slide-enter, &.slide-leave-to
    transform translate3d(100%, 0, 0)

  .image-header
    position relative
    width 100%
    height 0
    padding-top 100%

    img
      position absolute
      top 0
      left 0
      width 100%
      height 100%

    .back
      position absolute
      left 0
      top 0

      .icon-arrow_lift
        display block
        padding 20px
        font-size 20px
        color #fff

  .content
    padding 18px
    position relative

    .title
      line-height 14px
      margin-bottom 8px
      font-size 14px
      font-weight 700
      color rgb(7, 17, 27)

    .detail
      margin-bottom 18px
      line-height 10px
      height 10px
      font-size 0

      .sell-count, .rating
        font-size 10px
        color rgb(147, 153, 159)

      .sell-count
        margin-right 12px

    .price
      font-weight 700
      line-height 24px
      font-size 0

      .new
        margin-right 8px
        font-size 14px
        color rgb(240, 20, 20)

      .old
        text-decoration line-through
        font-size 10px
        color rgb(147, 153, 159)

    .cartcontrol-wrapper
      position absolute
      right 12px
      bottom 12px

    .buy
      position absolute
      right 14px
      bottom 14px
      z-index 10
      line-height 24px
      height 24px
      padding 0 10px
      font-size 10px
      box-sizing border-box
      border-radius 12px
      color #ffffff
      background rgb(0, 160, 220)

      // opacity 0.5
      &.scale-enter-active, &.scale-leave-active
        transition all 0.3s ease-in-out

      &.scale-enter, &.scale-leave-to
        width 0
        opacity 0

  .info
    padding 18px

    .title
      margin-bottom 6px
      line-height 14px
      font-size 14px
      font-weight 700
      color rgb(7, 17, 27)

    .text
      padding 0 8px
      line-height 24px
      font-size 12px
      text-align justify
      color rgb(77, 85, 93)

  .rating
    padding-top 18px

    .title
      margin-left 18px
      line-height 14px
      font-size 14px
      font-weight 700
      color rgb(7, 17, 27)

    .rating-wrapper
      padding 0 18px

      .rating-item
        padding 16px 0
        border-1px(rgba(7, 17, 27, 0.1), 'bottom')

        .user
          position absolute
          right 0
          top 16px
          line-height 12px
          font-size 0

          .name
            display inline-block
            margin-right 6px
            vertical-align top
            font-size 10px
            color rgb(147, 153, 159)

          .avatar
            border-radius 50%

        .time
          margin-bottom 6px
          line-height 12px
          font-size 10px
          color rgb(147, 153, 159)

        .text
          line-height 16px
          font-size 12px
          color rgb(7, 17, 27)

          .icon-thumb_up, .icon-thumb_down
            margin-right 4px
            line-height 16px
            font-size 12px

          .icon-thumb_up
            color rgb(0, 160, 220)

          .icon_thumb_down
            color rgb(147, 153, 159)

      .no-rating
        padding 16px 0
        font-size 12px
        color rgb(147, 153, 159)
</style>



